import React from "react";
import CustomPrimaryButton from "../../shared/components/CustomPrimaryButton";
import RedirectInfo from "../../shared/components/RedirectInfo";
import { useHistory } from "react-router-dom";
import { Tooltip } from "@mui/material";

const getFormNotValidMessage = () => {
  return "Entrer une adresse e-mail correcte entre 6 et 12 caractres";
};

const getFormValidMessage = () => {
  return "Appuyer pour connecter!";
};

const LoginPageFooter = ({ handleLogin, isFormValid }) => {
  const history = useHistory();

  const handlePushToRegisterPage = () => {
    history.push("/register");
  };

  const handlePushToHomePage = () => {
    history.push("/");
  }

  return (
    <>
      <Tooltip
        title={!isFormValid ? getFormNotValidMessage() : getFormValidMessage()}
      >
        <div>
          <CustomPrimaryButton
            label="Se connecter"
            additionalStyles={{ marginTop: "30px" }}
            disabled={!isFormValid}
            onClick={handleLogin}
          />
        </div>
      </Tooltip>
      <RedirectInfo
        text="Pour s'inscrire? "
        redirectText="Registration"
        additionalStyles={{ marginTop: "5px" }}
        redirectHandler={handlePushToRegisterPage}
      />

      <RedirectInfo
        text="Accueil : "
        redirectText="Page web"
        additionalStyles={{ marginTop: "5px" }}
        redirectHandler={handlePushToHomePage}
      />
    </>
  );
};

export default LoginPageFooter;
