import React from 'react';
import {Link} from 'react-router-dom';
import "./HomeStyle.css";
import icon1 from "./image/btob.png";
import icon2 from "./image/discord.png";
import icon3 from "./image/follow.png";
import icon4 from "./image/podcast.png";
import icon5 from "./image/recherche.png";
import icon6 from "./image/wifi.png";
import appercu from "./image/ProgResApp1.png";
import contact from "./image/Contact.jpg";

function HomePage() {
  return (
    <div className="homePage" id="Accueil">
      <div className="header">
        <div className="navbar">
          <div className="logo">
            <h1>K<span>c</span>-Gr<span>a</span>m</h1>  
          </div>
          <div className="parcourir">
            <ul>
              <li><a href='#Accueil' className="link__mod">Accueil</a></li>
              <li><a href='#Description' className="link__mod">Description</a></li>
              <li><a href='#Contact' className="link__mod">Contact</a></li>
              <li><a href='#Propos' className="link__mod">A propos</a></li>
            </ul>
          </div>
          <div className="login-btn">
            <Link to='/login' className="link__btn">Se connecter</Link>
          </div>
        </div>

        <div className="header-content">
          <p>On vous propose une plateforme de discussion facile  utiliser, une prise en main confortable. Elle est gratuit et ont des nombreuses fonctionnalits.</p>
          Pour s'inscrire veuillez cliquer ici : <Link to='/register'>s'inscrire</Link>
        </div>

        <img className="icon1" src={icon1} alt=""/>
        <img className="icon2" src={icon2} alt=""/>
        <img className="icon3" src={icon3} alt=""/>
        <img className="icon4" src={icon4} alt=""/>
        <img className="icon5" src={icon5} alt=""/>
        <img className="icon6" src={icon6} alt=""/> 
      </div>

      <div className="main">
        <div className="description">
          <h2 className="titre__desc" id="Description">Description :</h2>
          <div className="desc_content">
            <div className='desc_left'>
              <p>L'application comporte :</p>
              <ul>
                <li>Une authentification fiable</li>
                <li>Invitation d'amis</li>
                <li>Chat-app ou discussion entre amis</li>
                <li>Appel-video avec audio ou sans audio et vis versa</li>
                <li>Partage de l'cran, utile pour l'entraide</li>
              </ul>

              <p>Objectif de l'application :</p>
              <ul>
                <li>Pour faciliter la communication entre les amis</li>
                <li>Pour l'entraide qui est la rle principale de la partage ecran</li>
                <li>Connectivit en surett avec la fonction message prive</li>
                <li>Alternative app de communication en cas de besoins</li>
              </ul> 

              <p>Elle comporte deux types d'utilisation :</p>
              <ul>
                <li>En local</li>
                <li>Sur internet</li>
              </ul> 

              <h2>NB: En tant qu'tudiant, On a develloper cette application pour facilit l'entraide entre tudiant.</h2>
            </div>
            <div className='desc_right'>
              <h2 className='titre_right'>Appercus de l'application :</h2>
              <img className="appercu_img" src={appercu} alt=''/>
            </div>
          </div>
        </div>

        <div className="contact" id="Contact">
            <h2 className="titre_contact">Contact :</h2>
            <div className='contact_content'>
              <div className="contact_left">
                  <img className="contact_img" src={contact} alt=''/>
              </div>

              <div className="contact_right">
                  <h2>Telephone : </h2>
                      034 94 220 85
                  <h2>Email : </h2>
                      Jharinel10@gmail.com
                  <h2>Localisation : </h2>
                      <p>Anosizato, Antananarivo</p>
                      <p>Andrainjato, Fianarantsoa</p>        
              </div>
            </div>
        </div>
      </div>

      <div className="footer" id='Propos'>
        <div className="footer-left"></div>
        <div className="footer-center"></div>
        <div className="footer-bottom"></div>
      </div>
    </div>
  )
}

export default HomePage